import React, { useState, useMemo, useEffect, memo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Button, Typography } from "@material-ui/core";
import {
  addEmployee,
  removeEmployee,
  removeAllEmployee
} from "./utils/actions";
import { PageContainer } from "./styles/PageContainer";
import { CreateEmployeeContainer } from "./styles/CreateEmployeeContainer";
import { Table } from "./components/Table";
import { InputTextField } from "./components/InputTextField";
import { delay } from "./utils/delay";

const App = memo(() => {
  const dispatch = useDispatch();
  const { employees } = useSelector((state) => {
    return { employees: state.employees };
  });
  const [inputName, setInputName] = useState("");
  const [inputLastName, setInputLastName] = useState("");
  const [inputAge, setInputAge] = useState("");
  const [data, setData] = useState(employees);
  const columns = useMemo(
    () => [
      {
        minWidth: 178,
        resizable: true,
        Header: "Имя",
        accessor: "firstName"
      },
      {
        minWidth: 177,
        resizable: true,
        Header: "Фамилия",
        accessor: "lastName"
      },
      {
        minWidth: 177,
        resizable: true,
        Header: "Возраст",
        accessor: "age"
      },
      {
        id: "delete",
        accessor: () => "delete",
        width: 130,
        Header: (tableProps) => {
          const { isAllRowsSelected } = tableProps;
          if (isAllRowsSelected) {
            return (
              <Button
                size="small"
                variant="contained"
                color="secondary"
                onClick={() => {
                  delay(3000).then(() => dispatch(removeAllEmployee()));
                }}
              >
                Удалить всё
              </Button>
            );
          }
          return null;
        },
        Cell: (tableProps) => (
          <Button
            size="small"
            variant="contained"
            color="secondary"
            onClick={() => {
              const { index: rowIndex } = tableProps?.row;
              const dataId = tableProps?.data[rowIndex]?.id;
              const { rows } = tableProps;
              const selectedRows = rows
                .filter((row) => {
                  const isSelected = row.isSelected;
                  if (isSelected) return row.original.id;
                  return false;
                })
                .map((row) => row.original.id);
              if (dataId)
                delay(3000).then(() =>
                  dispatch(removeEmployee(dataId, rowIndex, selectedRows))
                );
            }}
          >
            Удалить
          </Button>
        )
      }
    ],
    []
  );

  const handleCreateEmployee = () => {
    delay(3000).then(() =>
      dispatch(addEmployee(inputName, inputLastName, inputAge))
    );
    setInputName("");
    setInputLastName("");
    setInputAge("");
  };
  const handleOnChange = (value, func) => {
    func(value);
  };

  useEffect(() => {
    setData(employees);
  }, [employees]);

  return (
    <PageContainer>
      <Typography variant="h5" gutterBottom>
        Создать сотрудника:
      </Typography>
      <CreateEmployeeContainer>
        <InputTextField
          required
          id="name"
          label="Имя"
          variant="outlined"
          value={inputName}
          func={setInputName}
          handleOnChange={(value, func) => handleOnChange(value, func)}
        />
        <InputTextField
          required
          id="last-name"
          label="Фамилия"
          variant="outlined"
          value={inputLastName}
          func={setInputLastName}
          handleOnChange={(value, func) => handleOnChange(value, func)}
        />
        <InputTextField
          required
          type="number"
          id="age"
          label="Возраст"
          variant="outlined"
          value={inputAge}
          func={setInputAge}
          handleOnChange={(value, func) => handleOnChange(value, func)}
        />
        <Button
          variant="contained"
          color="primary"
          onClick={handleCreateEmployee}
          disabled={!inputName || !inputLastName || !inputAge}
        >
          Создать
        </Button>
      </CreateEmployeeContainer>
      <Table columns={columns} data={data} />
    </PageContainer>
  );
});

export default App;
